@import '../common/global-set';
.outer{
	width:100%;
	margin-top:18px;
	border-top: solid 9px #54BEB2;
	color:black;
}
.global_center{
	width:1042px;
	margin:0 auto;
}
.vertical_line{
	margin-left:43px;
	border-left:solid 8px #54BEB2;
	border-right:solid 8px #54BEB2;
	width:82px;
	height:36px;
}
.list_text{
	width:164px;
	height:47px;
	line-height:47px;
	font-size:22px;
	text-align:center;
	font-weight:500;
	background-color:#54BEB2;
	color:white;
	float:left;
}
.list img{
	margin:15px 15px 54px 15px;
	float:left;
}
.list p{
	font-weight:500;
	margin-top:7px;
	font-size:26px;	
}
.title{
	clear:both;
	background-color:#497D92;
	width:100%;
	height:58px;
	color:white;
	line-height:58px;
}
.title p{
	font-size:20px;
	margin-left:40px;
	font-weight:300;
	float:left;
}
#title_text{
	text-align:center;
	font-size:22px;
	line-height:58px;
	width:908px;
}
.content{
	width:1042px;
	margin-top:15px;
	overflow:hidden;
	margin-bottom:30px;
}
#text_time{
	margin:0 50px 0 40%;
	float:left;
}
#content_text{
	text-overflow:ellipsis;
	height:690px;
	font-size:16px;
	line-height:28px;
	margin-top:40px;
	width:100%;
}
.tag{
	width:100%;
	height:67px;
	background-color:#54BEB2;
	text-align:center;
}
.tag div{
	margin:0 auto;
	width:480px;
	height:67px;
	line-height:67px;
}
.tag a{
	font-size:22px;
	color:white;
	margin-left:60px;
	margin-right:60px;
	text-decoration:none;
	float:left;
}
.tag p{
	font-size:16px;
	color:black;
	float:left;
	font-weight:700;
}
.tag input{
	float:left;
	height:24px;
	width:25px;
	font-size:20px;
	margin:21px 17px 0 17px;
	padding-left:5px;
	line-height:25px;
}